@import "./main.scss";





.slick-slider.slick-initialized{
  width: 100%;
  box-shadow: 0 0 10px 1px;
  border: 1px solid;
  >button{
    background:brown;
    width: 50px;
    height: 50px;
    z-index: 9;
    &::before{
      padding:.5em;
    }
    &:first-of-type{
        border-radius: 100px 0 0 100px;
    }
    &:last-of-type{
      border-radius: 0 100px 100px 0;
    }
  }
  >div.slick-list{
    height: 700px !important;
    >.slick-track{
      display: flex;
      align-items: center;
      height: 100%;
      >div.slick-slide{
        height: 100%;
        >div{
          height: 100%;

          display: flex;
          align-items: center;
          text-align: center;
          >section{
            height: 100%;
            width: 45%;
            >h1{}
            >h4{}
            >p{}
            >img{object-fit: contain;}
          }
        }
      }
    }
  }
  >ul{position: relative;bottom:0;}
}

